<template>
  <v-form v-model="valid">
    <v-text-field
      v-model="email"
      :rules="emailRules"
      :label="$t('email')"
      browser-autocomplete="username"
      required
    ></v-text-field>
    <v-text-field
      v-model="password"
      :label="$t('password')"
      type="password"
      browser-autocomplete="current-password"
      required
    ></v-text-field>
    <div class="dflex justify-space-around wrap">
      <div>
        <v-btn :loading="loginState === 0" color="success" @click="login">
          {{ $t("login") }}
        </v-btn>
      </div>
      <div>
        <v-btn replace to="register" color="info">
          {{ $t("goToRegister") }}
          <v-icon right small>fa-arrow-right</v-icon>
        </v-btn>
      </div>
    </div>
  </v-form>
</template>

<script>
import { emailRules } from "../utils/form/rules";

import { login } from "../store/user";

export default {
  data: () => ({
    valid: true,

    email: "",
    password: "",

    loginState: 1,
    errMsg: "",

    emailRules
  }),

  methods: {
    async login() {
      if (!this.valid) {
        this.$alertInfo(this.$t("pleaseInputValid"));
        return;
      }

      this.loginState = 0;

      try {
        await login({ email: this.email, password: this.password });
        this.loginState = 1;
      } catch (error) {
        this.$alertError(error);
        this.errMsg = error && error.message;
        this.loginState = -1;
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '../styles/flex';
</style>
